// auth.js
const API_BASE_URL = "https://api-verifier.onrender.com";
const FRONTEND_BASE_URL = "https://quader864.github.io";

async function verifySession(redirectIfUnauthorized = true) {
  try {
    const res = await fetch(`${API_BASE_URL}/verify`, {
      method: "POST",
      credentials: "include"
    });
    const data = await res.json();

    if (!res.ok || data.status !== "authorized") {
      if (redirectIfUnauthorized) {
        window.location.href = `${FRONTEND_BASE_URL}/login.html`;
      }
      return null;
    }

    return data.user; // return user data if authorized
  } catch (err) {
    console.error("Session verification failed:", err);
    if (redirectIfUnauthorized) {
      window.location.href = `${FRONTEND_BASE_URL}/login.html`;
    }
    return null;
  }
}
